import Checkbox from "../Checkbox/Checkbox"
import { useState } from "react";
import "../Input/input.css"
import Input from "../Input/Input";
import TextArea from "../TextArea/TextArea";
import Radiobutton from "../Radiobutton/Radiobutton";

const Anketa = ({isFriends}) => {
  const [fio, setFio] = useState("")
  const [secondName, setSecondName] = useState("")
  const [musicList, setMusicList] = useState("")
  const [isZags, setIsZags] = useState(false);
  const [isBunket, setIsBunket] = useState(false);
  const [isParty, setIsParty] = useState(false);
  const [salad, setSalad] = useState("")
  const [hotDish, setHotDish] = useState("")

  const [fioInvalid, setFioInvalid] = useState(false)

  const [isLoading, setIsLoading] = useState(false)

  const [result, setResult] = useState("")

  function BadRequest() {
    setIsLoading(false)
    setResult("    .  ")
  }

  function onSubmit(e) {
    if (isLoading)
      return;
    setResult("")
    if (validation()) {
      setIsLoading(true)
      try {
        let request = {
          name: fio,
          secondName: secondName,
          songs: musicList,
          zags: isZags,
          bunket: isBunket,
          party: isParty,
          salad,
          hotDish
        }
        fetch("https://mywedding.somee.com/accepts", {
          method: "POST", 
          body: JSON.stringify(request),
          headers: {
            'Content-Type': 'application/json;charset=utf-8'
          },
        })
          .then(result => {
            if (result.ok) {
              setIsLoading(false)
              setResult("    !   -    ,      ")
            } else {
              BadRequest()
            }
          }, () => {
            BadRequest()
          })
      }
      catch (e) {
        BadRequest()
      }
    }
    
  }

  function validation() {
    let invalid = !fio
    setFioInvalid(invalid)
    return !invalid
  }

  return (
    <div className="section_questionnaire">
      <div className="section_questionnaire-title">
          
      </div>
      <div className="section_questionnaire-subtitle">
                 .
      </div>
      <form className="section_questionnaire_form">
        <Input invalid={fioInvalid} value={fio} setValue={setFio} placeholder={""} label={"  :"}/>
        <Input value={secondName} setValue={setSecondName} placeholder={""} label={"  :"}/>
        <Checkbox checked={isZags} label={"   "} setIsChecked={setIsZags}/>
        <Checkbox checked={isBunket} label={"   "} setIsChecked={setIsBunket}/>
        {isFriends && <Checkbox checked={isParty} label={"       "} setIsChecked={setIsParty}/>}
        <TextArea value={musicList} setValue={setMusicList} placeholder={"    "} label={"    ,   :"}/>
        <div> ,        </div>
        <div></div>
          <Radiobutton setLabel={setSalad} name="salad" label={"    "} />
          <Radiobutton setLabel={setSalad} name="salad" label={"   "} />
        <div></div>
          <Radiobutton setLabel={setHotDish} name="hotdish" label={"       "} />
          <Radiobutton setLabel={setHotDish} name="hotdish" label={"   "} />
        <div onClick={onSubmit} className='section_questionnaire_form-submit'>
          {isLoading ? <div className="loader"></div>: " "}
        </div>

        {result && <div style={{textAlign: 'center', marginTop: 40}}>{result}</div>}
      </form>
    </div>
  )
}

export default Anketa